@import url(https://fonts.googleapis.com/css2?family=Inter:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{
  width: 85%;
  padding: 2rem;
  margin: auto;
  box-sizing: border-box;
  font-family: 'Inter', sans-serif;
}
.share{
  width: 80%;
  display: flex;
  justify-content: end;
}
.more{
  width: 80%;
  display: flex;
  justify-content: end;
  display: none;
}
.pic{
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.pic #twitter{
  font-weight: bold;
}
.pic #slack{
  display: none;
}
.main{
  margin: 2rem 0;
}
.icon{
  width: 100%;
  display: flex;
  justify-content: center;
  grid-gap: 1rem;
  gap: 1rem;
  padding: 2rem 0;
}
hr{
  margin: 1rem 0;
  border:1px solid #EAECF0;
}
footer{
  color: #667085;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

@media (max-width:997px) {
  .App{
    width: 100%;
  }
  .share{
    display: none;
  }
  .more{
    display: flex;
  }
  footer{
    
    flex-wrap: wrap;
  }
}
.btn-container{
    width: 95%;
    margin: auto;
}
.btn{
    display: flex;
    padding: 1.2rem;
    margin: 1rem 0;
    background-color: #EAECF0;
    color: #101828;
    border-radius: 10px;
    text-decoration: none;
    justify-content: center;
    font-size: 18px;
}
@media (max-width:997px) {
    .btn-container{
        width: 100%;
    }
    .btn{
        padding: 1rem;
        font-size: 14px;
    }
    
}
.contact-page{
    width: 80%;
    margin: auto;
    padding: 10px 0 20px 0;
    box-sizing: border-box;
}
.head-contact{
    width: 70%;
    margin: auto;
    padding: 1rem;
    font-size: 18px;
}
form{
    box-sizing: border-box;
    width: 100%;
    display: flex;
    flex-direction: column;
    grid-gap: 1rem;
    gap: 1rem;

}
.form-name{
    box-sizing: border-box;
    width: 99%;
    display: flex;
    justify-content: space-between;
    grid-gap: 1rem;
    gap: 1rem;

}
.fn{
    width: 100%;
}
input[type="text"], textarea{
    box-sizing: border-box;
    width: 99%;
    padding: 1.2rem 0;
    text-indent: 1rem;
    border-radius: 10px;
    outline: none;
    font-size: 18px;
    border: 1px solid #D0D5DD;
    margin-top: 10px;

}
.check{
    display: flex;
    grid-gap: 0.5rem;
    gap: 0.5rem;
}

.btn-contact button{
    box-sizing: border-box;
    width: 100%;
    padding: 1rem;
    background-color:  #1570EF;
    border-radius: 10px;
    border: none;
    color: #fff;
    font-size: 18px;
    margin-top: 10px;

}

@media (max-width:997px) {
    .contact-page{
        width: 90%;
        font-size: 25px;
        overflow-x: hidden;
    }
    .head-contact{
        width: 100%;
        padding: 0;
    }
    .form-name{
        flex-direction: column;
    }
    .check{
        align-items: baseline;
    }
    
}
